.navbar{
    display: flex;
    align-items: center;
    top: 0;
    position: sticky;
    z-index: 20;
    justify-content: center;
    background-color: rgb(241, 237, 233);
    transition: top 10s; 
    margin-bottom: 2vh;
}

.navbar-logo{
    margin-left: 4rem;
}

nav{
    display: flex;
    justify-content: space-evenly;
    width: 70%;
    font-size: large;
}
nav>a{
    text-decoration: none;
    align-items: center;
    color:black;
}