#projects {
    text-align: center;
}

.main-project-container{
    width: 75%;
    margin: 3% auto;
}

.main-project-container:hover .hover-buttons{
        visibility: visible;
}
.project-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2% auto;
}
.hover-buttons {
    display: flex;
    justify-content: space-evenly;
    visibility: hidden;
    width: 17%;
    margin: auto;
    background: rgb(188, 183, 183);
}
.hover-buttons a{
    text-decoration: none;
}


.hover-buttons i{
    font-size:x-large;
}

.project-container:hover>.hover-buttons {
    visibility: visible;
}

.project-container>img {
    width: 55%;
}

.content-container {
    display: flex;
    flex-direction: column;
    text-align: initial;
    padding: 0px 2vw;
}